*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content{
  height: 100vh;
}

.register-page{
  background-image: linear-gradient(to right,#0081ffad,#58008078);
  position: absolute;
  top: 14%;
  left : 34% ;
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70vh;
  border-radius: 13px;
}

/* transaction-page */
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray ;
}

.ant-icon svg{
  font-size: 20px;
  cursor: pointer;
}

.active-icon{
  color: black;
}

.inactive-icon{
  color: grey;
}

.switch-icon{
  border: 1px solid rgba(0,0,0,0.677);
  border-radius: 5px;
  padding: 10px 15px;
}

.footer{
  position: absolute;
  bottom: 0;
  left:20%
}

.app-name{
  background-image: linear-gradient(#0081ffad,#58008078);
  font-size: 40px;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.register-heading{
  background-image: linear-gradient(#0f151aad,#170e1c78);
    font-size: 40px;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
a{
  color: #4f4b7c !important;
}
a:hover {
  color : white !important;
}

a:visited{
  color: purple !important;
}

.circles{
  display: flex;
  flex-direction: column;

}

.expense{
  overflow-y: auto;
}